body {
  margin: 0;
  padding: 0;
  font-family: Calibri;
  margin-top: 100px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
}

.todo-list-main {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 720px;
  width: 400px;
  background-color: #9370DB;
  border-radius: 25px;
}

.input-button{
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  padding-top: 25px;
  padding-bottom: 15px;
  background-color: #ab8cea;
  border-radius: 15px;
}

.input-button input {
  background-color: #cccccf;
  width: 250px;
  height: 25px;
  border-radius: 15px;
  text-align: center;
}

.input-button button {
  width: 100px;
  height: 25px;
  margin-top: 20px;
  border-radius: 50px;
}

.list-todo ul li{
  margin-right: 45px;
  word-wrap: break-word;
}


